<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> A boolean Property specifying the visibility of the volume.</td>
</tr>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the array of Cartesian3 positions which define the line strip. <strong>struct: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>shape</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the array of Cartesian2 positions which define the shape to be extruded. <strong>struct: [{ x: number, y: number },...,{ x: number, y: number }]</strong></td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A CornerType Property specifying the style of the corners. <strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A numeric Property specifying the angular distance between each latitude and longitude point.</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> A boolean Property specifying whether the volume is filled with the provided material.</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the material used to fill the volume.</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> A boolean Property specifying whether the volume is outlined.</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the Color of the outline.</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td>1.0</td>
<td><code v-pre>optional</code> A numeric Property specifying the width of the outline.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> An enum Property specifying whether the box casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying at what distance from the camera that this volume will be displayed. <strong>struct: { near: number, far: number }</strong></td>
</tr>
</tbody>
</table>
